export default `/* 
*  padding:30px;
*  font-family:optima-Regular;
*  word-break:break-all;
color:#595959;
*/
#nice {
  line-height: 1.75;
  color: #595959;
  font-family: Optima-Regular, Optima, PingFangTC-Light, PingFangSC-light, PingFangTC-light;
  letter-spacing: 2px;
  background-image: linear-gradient(90deg, rgba(50, 0, 0, 0.05) 3%, rgba(0, 0, 0, 0) 3%), linear-gradient(360deg, rgba(50, 0, 0, 0.05) 3%, rgba(0, 0, 0, 0) 3%);
  background-size: 20px 20px;
  background-position: center center;
}

/* 
*  margin-top:5px;
*  margin-bottom:5px;
*  line-height:26px;
*  word-spacing:3px;
*  letter-spacing:3px;
*  text-align:left;
*  color:#3e3e3e;
*  font-size:16px;
*  text-indent:2em;
*/
#nice p {
  color: #595959;
  margin: 10px 0px;
  letter-spacing: 2px;
  font-size: 14px;
  word-spacing: 2px;
}

/*  */
#nice h1 {
  font-size: 25px;
}

/*  */
#nice h1 .content {
  display: inline-block;
  font-weight: bold;
  color: #595959;
}

/*   */
#nice h1:after {}

/*  */
#nice h2 {
  text-align: left;
  margin: 20px 10px 0px 0px;
}

/*  */
#nice h2 .content {
  font-size: 18px;
  font-weight: bold;
  display: inline-block;
  padding-left: 10px;
  border-left: 5px solid #DEC6FB;
  color: #595959;

}

/*   */
#nice h2:after {}

/*  */
#nice h3 {
  font-size: 16px;
  font-weight: bold;
  text-align: center;
}

/*  */
#nice h3 .content {
  border-bottom: 2px solid #DEC6FB;
  color: #595959;
}

/*   */
#nice h3:after {}

/* 
* list-style-type: square|circle|disc;
*/
#nice ul {
  font-size: 15px; /*li section*/
  color: #595959;
  list-style-type: circle;
}


/* 
* list-style-type: upper-roman|lower-greek|lower-alpha;
*/
#nice ol {
  font-size: 15px;
  color: #595959;
}

/* li
*/
#nice li section {
  font-size: 14px;
  font-weight: normal;
  color: #595959;
}

/* 
*  border-left-color:black;
*  background:gray;
*/
#nice blockquote::before {
  content: "";
  /*   color: #d9b8fa;*/
  color: #DEC6FB;
  font-size: 34px;
  line-height: 1;
  font-weight: 700;
}

#nice blockquote {
  text-size-adjust: 100%;
  line-height: 1.55em;
  font-weight: 400;
  border-radius: 6px;
  color: #595959;
  font-style: normal;
  text-align: left;
  box-sizing: inherit;
  border-left: none;
  border: 1px solid #DEC6FB;
  background: #F6EEFF;

}

#nice blockquote p {
  color: #595959;
}

#nice blockquote::after {
  content: "";
  float: right;
  /*   color: #d9b8fa; */
  color: #DEC6FB;
}

/*  
* border-bottom: 1px solid #009688;
*/
#nice a {
  color: #664D9D;
  font-weight: normal;
  border-bottom: 1px solid #664D9D;
}

#nice strong::before {
  content: '';
}

/*  */
#nice strong {
  color: #595959;
  font-weight: bold;
}

#nice strong::after {
  content: '';
}

/*  */
#nice em {
  font-style: normal;
  color: #595959;
  background: #F6EEFF;
}

/*  */
#nice em strong {
  color: #595959;
}

/*  */
#nice del {
  color: #595959;
}

/* 
* 
* border-top:1px solid #3e3e3e;
*/
#nice hr {
  height: 1px;
  padding: 0;
  border: none;
  border-top: 2px solid #d9b8fa;
}

/* 
*  width:80%;
*  margin:0 auto;
*  margin:0 0;
*/
#nice img {
  border-radius: 6px;
  display: block;
  margin: 20px auto;
  object-fit: contain;
}

/*  */
#nice figcaption {
  display: block;
  font-size: 13px;
  color: #595959;
}

/*  */
#nice p code,
#nice li code {
  color: #595959;
}

/* 
*  display:-webkit-box !important;
*  display:block;
*/
#nice .code-snippet__fix {
  background: #f7f7f7;
  border-radius: 2px;
}

#nice pre code {
  /* background: #f7f7f7; */
}

/*
* 
*  font-size: 16px;
*  border: 1px solid #ccc;
*  padding: 5px 10px;
*/
#nice table tr th,
#nice table tr td {
  font-size: 14px;
  color: #595959;
}

#nice .footnotes {
  background: #F6EEFF;
  padding: 20px 20px 20px 20px;
  font-size: 14px;
  border: 0.8px solid #DEC6FB;
  border-radius: 6px;
  border: 1px solid #DEC6FB;
}

/*  */
#nice .footnote-word {
  font-weight: normal;
  color: #595959;
}

/*  */
#nice .footnote-ref {
  font-weight: normal;
  color: #595959;
}

/**/
#nice .footnote-item em {
  background: #F6EEFF;
  font-size: 14px;
  color: #595959;
  display: block;
}

/* "" 
*  content: "";
*/
#nice .footnotes-sep:before {
  content: 'Reference';
  color: #595959;
  letter-spacing: 1px;
  border-bottom: 2px solid #DEC6FB;
  display: inline;
  background: linear-gradient(white 60%, #F6EEFF 40%);
  font-size: 20px;
}

/*  */
#nice .footnote-num {}

/*  */
#nice .footnote-item p {
  color: #595959;
  font-weight: bold;
}

/*  */
#nice .footnote-item p em {
  font-weight: normal;
}

/* 
*  max-width: 300% !important;
*/
#nice .block-equation svg {}

/* 
*/
#nice .inline-equation svg {}`;
