export default `/* 
*  padding:30px;
*  font-family:optima-Regular;
*  word-break:break-all;
*/
#nice {
  line-height: 1.25;
  font-family: Optima-Regular, Optima, PingFangTC-Light, PingFangSC-light, PingFangTC-light;
  letter-spacing: 2px;
  background-image: linear-gradient(90deg, rgba(50, 0, 0, 0.05) 3%, rgba(0, 0, 0, 0) 3%), linear-gradient(360deg, rgba(50, 0, 0, 0.05) 3%, rgba(0, 0, 0, 0) 3%);
  background-size: 20px 20px;
  background-position: center center;
}

/* 
  *  margin-top:5px;
  *  margin-bottom:5px;
  *  line-height:26px;
  *  word-spacing:3px;
  *  letter-spacing:3px;
  *  text-align:left;
  *  color:#3e3e3e;
  *  font-size:16px;
  *  text-indent:2em;
  */
#nice p {
  margin: 10px 0px;
  letter-spacing: 2px;
  font-size: 14px;
  word-spacing: 2px;
}

/*  */
#nice h1 {
  font-size: 25px;
}

/*  */
#nice h1 .content {
  display: inline-block;
  font-weight: bold;
  color: #773098;
}

/*   */
#nice h1:after {}

/*  */
#nice h2 {
  text-align: left;
  margin: 20px 10px 0px 0px;
}

/*  */
#nice h2 .content {
  font-size: 18px;
  font-weight: bold;
  display: inline-block;
  padding-left: 10px;
  border-left: 5px solid #916dd5;
}

/*   */
#nice h2:after {}

/*  */
#nice h3 {
  font-size: 16px;
  font-weight: bold;
  text-align: center;
}

/*  */
#nice h3 .content {
  border-bottom: 2px solid #d89cf6;
}

/*   */
#nice h3:after {}

/* 
  * list-style-type: square|circle|disc;
  */
#nice ul {
  font-size: 15px;
  /*li section*/
  list-style-type: circle;
}


/* 
  * list-style-type: upper-roman|lower-greek|lower-alpha;
  */
#nice ol {
  font-size: 15px;
}

/* li
  */
#nice li section {
  font-size: 14px;
  font-weight: normal;
}

/* 
  *  border-left-color:black;
  *  background:gray;
  */
#nice .multiquote-1 {
  border-left-color: #d89cf6;
  background: #f4eeff;
}

/*  
  * border-bottom: 1px solid #009688;
  */
#nice a {
  color: #916dd5;
  font-weight: bolder;
  border-bottom: 1px solid #916dd5;
}

#nice strong::before {
  content: '';
}

/*  */
#nice strong {
  color: #916dd5;
  font-weight: bold;
}

#nice strong::after {
  content: '';
}

/*  */
#nice em {
  font-style: normal;
  color: #916dd5;
}

/*  */
#nice em strong {
  color: #916dd5;
}

/*  */
#nice del {
  color: #916dd5;
}

/* 
  * 
  */
#nice hr {
  height: 1px;
  padding: 0;
  border: none;
  border-top: 2px solid #d9b8fa;
}

/* 
  *  width:80%;
  *  margin:0 auto;
  *  margin:0 0;
  */
#nice img {
  border-radius: 6px;
  display: block;
  margin: 20px auto;
  object-fit: contain;
  box-shadow: 2px 4px 7px #999;
}

/*  */
#nice figcaption {
  display: block;
  font-size: 13px;
}

/*  */
#nice p code,
#nice li code {
  color: #916dd5;
  font-weight: bolder;
  background: none;
}

/* 
  *  display:-webkit-box !important;
  *  display:block;
  */
#nice .code-snippet__fix {
  background: #f7f7f7;
  border-radius: 2px;
}

#nice pre code {}

/*
  * 
  *  font-size: 16px;
  *  border: 1px solid #ccc;
  *  padding: 5px 10px;
  */
#nice table tr th,
#nice table tr td {
  font-size: 14px;
}

#nice .footnotes {
  font-size: 14px;
}

/*  */
#nice .footnote-word {
  font-weight: normal;
  color: #916dd5;
  font-weight: bold;
}

/*  */
#nice .footnote-ref {
  font-weight: normal;
  color: #916dd5;
}

/**/
#nice .footnote-item em {
  font-size: 14px;
  color: #916dd5;
  display: block;
}

/* "" 
  *  content: "";
  */
#nice .footnotes-sep:before {
  font-size: 20px;
}

/*  */
#nice .footnote-num {
  color: #916dd5;
}

/*  */
#nice .footnote-item p {
  color: #916dd5;
  font-weight: bold;
}

/*  */
#nice .footnote-item p em {
  font-weight: normal;
}

/* 
  *  max-width: 300% !important;
  */
#nice .block-equation svg {}

/* 
  */
#nice .inline-equation svg {}
`;
