import React, {Component} from "react";
import {observer, inject} from "mobx-react";
import {message, Tooltip} from "antd";

import {solveHtml, solveJuejinMath, juejinSuffix, solveMermaid, copySafari} from "../../utils/converter";
import {LAYOUT_ID, CODE_NUM, ENTER_DELAY, LEAVE_DELAY} from "../../utils/constant";
import SvgIcon from "../../icon";
import "./Juejin.css";

@inject("content")
@inject("navbar")
@inject("imageHosting")
@inject("dialog")
@observer
class Juejin extends Component {
  constructor(props) {
    super(props);
    this.html = "";
  }

  copyJuejin = async () => {
    if (window.localStorage.getItem(CODE_NUM) === "0") {
      message.warning("");
      return;
    }
    const layout = document.getElementById(LAYOUT_ID); // 
    const html = layout.innerHTML;
    solveJuejinMath();
    juejinSuffix();
    await solveMermaid();
    this.html = solveHtml();
    copySafari(this.html);
    message.success(" mdnice ");
    layout.innerHTML = html; // 
  };

  render() {
    return (
      <Tooltip placement="left" mouseEnterDelay={ENTER_DELAY} mouseLeaveDelay={LEAVE_DELAY} title="">
        <a id="nice-sidebar-juejin" className="nice-btn-juejin" onClick={this.copyJuejin}>
          <SvgIcon name="juejin" className="nice-btn-juejin-icon" />
        </a>
      </Tooltip>
    );
  }
}

export default Juejin;
